.loader_website {
  z-index: 1100;
  width: 100%;
  height: 100%;
  -o-transition: ease-in-out .1s;
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  transition: all .1s ease-in-out;
  animation: .2s loader_show;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.loader_website * {
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
}

body.loader .loader_website span {
  top: 18%;
}

.loader_website > span {
  width: 48px;
  height: 48px;
  -o-border-radius: 100%;
  -ms-border-radius: 100%;
  -o-transition: ease-in-out .1s;
  -o-box-shadow: #000 0px 5px 10px -5px;
  -ms-box-shadow: #000 0px 5px 10px -5px;
  background-color: #fff;
  border-radius: 100%;
  margin-left: -24px;
  padding: 4px;
  transition: all .1s ease-in-out;
  display: block;
  position: absolute;
  top: -50px;
  left: 50%;
  box-shadow: 0 5px 10px -5px #000;
}

.loader_website > span > svg {
  fill: #0000;
  stroke: #ff001b;
  stroke-width: 5px;
  animation: 2s infinite loader_dash, 2s linear infinite loader_rotate;
}

.loader_website > span.ios {
  height: 48px;
  width: 48px;
  -o-border-radius: 10px;
  -ms-border-radius: 10px;
  border-radius: 10px;
  animation: none;
}

.loader_website span.ios > svg {
  fill: #ff001b;
  stroke-width: 0;
  height: 100%;
  width: 100%;
  animation: none;
}

.loader_website > span.ios > svg path:nth-of-type(1) {
  animation: 1s linear infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(2) {
  animation: 1s linear -.083s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(3) {
  animation: 1s linear -.166s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(4) {
  animation: 1s linear -.249s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(5) {
  animation: 1s linear -.332s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(6) {
  animation: 1s linear -.415s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(7) {
  animation: 1s linear -.498s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(8) {
  animation: 1s linear -.581s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(9) {
  animation: 1s linear -.664s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(10) {
  animation: 1s linear -.747s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(11) {
  animation: 1s linear -.83s infinite loader_pulse;
}

.loader_website > span.ios > svg path:nth-of-type(12) {
  animation: 1s linear -.913s infinite loader_pulse;
}

@keyframes loader_dash {
  0% {
    stroke-dasharray: 1 95;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 85 95;
    stroke-dashoffset: -25px;
  }

  100% {
    stroke-dasharray: 85 95;
    stroke-dashoffset: -93px;
  }
}

@keyframes loader_rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader_pulse {
  50% {
    fill: #1a73e8;
  }

  to {
    fill: #1a73e866;
  }
}

@keyframes loader_show {
  0% {
    opacity: 0;
    transform: scale(1.2);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulsate-bck {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(.95);
  }

  100% {
    transform: scale(1);
  }
}

